import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import { Empty } from 'antd';

const LineWithLabelChart = props => {
  const { categories, series } = props;
  const [chartData, setChartData] = useState();

  useEffect(() => {
    const data = {
      series: [
        {
          name: "",
          data: series ?? [],
        },
      ],
      options: {
        chart: {
          height: 350,
          type: "line",
          dropShadow: {
            enabled: true,
            color: "#000",
            top: 18,
            left: 7,
            blur: 10,
            opacity: 0.2,
          },
          noData: {
            text: "No Data Found",
            align: 'center',
            verticalAlign: 'middle',
            offsetX: 0,
            offsetY: 0,
            style: {
              color: '#df4759',
              fontSize: '14px',
              fontFamily: undefined
            }
          },
          toolbar: {
            show: false,
          },
        },
        colors: ["#77B6EA", "#545454"],
        dataLabels: {
          enabled: true,
          style: {
            fontSize: '10px',
        },
        },
        stroke: {
          curve: "smooth",
        },
        title: {
          text: "",
          align: "left",
        },
        grid: {
          borderColor: "#e7e7e7",
          row: {
            colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
            opacity: 0.5,
          },
        },
        markers: {
          size: 1,
        },
        xaxis: {
          categories: categories ?? [],
          title: {
            text: "",
          },
          labels: {
            show: true,
            rotate: -45,
            rotateAlways: true,
            showDuplicates: false,
            trim: false,
            style: {
                fontSize: '10px',
            },
            offsetX: 4,
            offsetY: 0,
        },
        },
        yaxis: {
          title: {
            text: "",
          },
          min: 0,
          max: Math.max(...series) + 100,
        },
        legend: {
          position: "top",
          horizontalAlign: "right",
          floating: true,
          offsetY: -25,
          offsetX: -5,
        },
      },
    };
    setChartData(data);
  }, [categories, series]);

  return chartData ? (
    <Chart options={chartData.options} series={chartData.series} height={350} type="line" />
  ) : (
    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
  );
};

export default LineWithLabelChart;
